import React, { Component } from 'react';
import './App.css';

class App extends Component {

  constructor(props){
    super(props);
    this.state ={
      score:0,
      level:1,
      food:[],
      width:640,
      height:425,
      cell:10,
      d:'right',
      snake:[],
      currentCount:100,
      intervalId:0,
      name:'',
    }
    this.make_snake = this.make_snake.bind(this);
    this.make_food = this.make_food.bind(this);
    this.paint = this.paint.bind(this);
    this.timer = this.timer.bind(this);
    this.start = this.start.bind(this);
  }

  componentDidMount() {
    console.log("asda",this.props.location.search);
    var sr = [];
    sr = this.props.location.search.split("=")
    this.setState({name:sr[1]});
      const canvas = this.refs.canvas;
      this.ctx = canvas.getContext("2d");
      this.start();

    }

  componentWillUnmount() {

     clearInterval(this.state.intervalId);
  }

  timer() {

    this.paint();
    this.setState({ currentCount: this.state.currentCount -1 });
  }

  start(){

    clearInterval(this.state.intervalId);
    this.make_snake();
    this.make_food();


    var intervalId = setInterval(this.timer, 100);

    this.setState({intervalId: intervalId,score:0});

    document.onkeydown= this.handleKeyPress.bind(this);
  }

  make_snake()
  	{

  		var len = 5;
  		this.snake = [];
  		for(let i = len-1; i >=0 ; --i)
  		{

  			this.snake.push({x: i, y:0});
  		}
      //this.setState({snake});

  	}


    make_food()
    {

      this.food = {
        x: Math.round(Math.random()*(this.state.width-this.state.cell)/this.state.cell),
        y: Math.round(Math.random()*(this.state.height-this.state.cell)/this.state.cell),
      };

    }

   paint_cell(x, y, color)
  	{

  		this.ctx.fillStyle = color;
  		this.ctx.fillRect(x*this.state.cell, y*this.state.cell, this.state.cell, this.state.cell);
  		this.ctx.strokeStyle = "white";
  		this.ctx.strokeRect(x*this.state.cell, y*this.state.cell, this.state.cell, this.state.cell);
  	}

   paint()
  	{

      var snake = this.snake;

      //console.log("ctx",this.ctx);

      this.ctx.strokeStyle = "black";
      this.ctx.strokeRect(0, 0, this.state.width, this.state.height);
  		this.ctx.fillStyle = "white";
  		this.ctx.fillRect(0, 0, this.state.width, this.state.height);



  		var nx = snake[0].x;
  		var ny = snake[0].y;


  		if(this.state.d == "right") nx++;
  		else if(this.state.d == "left") nx--;
  		else if(this.state.d == "up") ny--;
  		else if(this.state.d == "down") ny++;



  		if(nx == -1 || nx == this.state.width/this.state.cell || ny == -1 || ny == this.state.height/this.state.cell || this.check_collision(nx, ny, snake))
  		{
        this.start();
  			return;
  		}


      var score = this.state.score;

  		if(nx == this.food.x && ny == this.food.y)
  		{
  			var tail = {x: nx, y: ny};
  			score++;
        this.setState({score})

  			this.make_food();
  		}
  		else
  		{
  			var tail = snake.pop();
  			tail.x = nx; tail.y = ny;
  		}


  		snake.unshift(tail);

      this.setState({snake});

  		for(var i = 0; i < snake.length; i++)
  		{
  			var c = snake[i];

  			this.paint_cell(c.x, c.y, "green");
  		}


      this.paint_cell(this.food.x, this.food.y, "red");

      var score_text = "Score: " + this.state.score;

      this.ctx.fillText(score_text, 5, this.state.height-5);

  }


  check_collision(x, y, array)
	{

		for(var i = 0; i < array.length; i++)
		{
			if(array[i].x == x && array[i].y == y)
			 return true;
		}
		return false;
	}


  handleKeyPress(event) {
    //console.log("adasd",event);
    var key = event.keyCode;
    var d='';
    if(key == "37" && this.state.d != "right") d = "left";
    else if(key == "38" && this.state.d != "down") d = "up";
    else if(key == "39" && this.state.d != "left") d = "right";
    else if(key == "40" && this.state.d != "up") d = "down";

    this.setState({d});
}


  render() {
    return (
      <div style={{textAlign:'center',background:'#e3e3e3'}}>
        <div >Welcome {this.state.name}</div>
        <canvas ref="canvas" width={640} height={425} style={{marginTop:30,}} />

      </div>
    );
  }
}

export default App;



